.quote-modal {
    position: fixed;
    width: 100%;
    height: calc(100% - 2.3em); /* make room for navbar */
    top: 0;
    left: 0;
 /*   background-color: black;
    opacity: 0.5;*/
    background: -webkit-gradient(linear, left top, left bottom, from(black), to(rgba(0,0,0,0)));
    background: -webkit-linear-gradient(black, rgba(0,0,0,0));
    background: -o-linear-gradient(black, rgba(0,0,0,0));
    background: linear-gradient(black, rgba(0,0,0,0));;
    display: -ms-flexbox;;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.quote-prompt-text {
    text-transform: uppercase;
    text-align: center;
    color: white;
    width: 75%;
    min-width: 100px;
    opacity: 1;
}

.quote-text {
    font-size: 1.7em;
    text-transform: uppercase;
    text-align: center;
    color: white;
    width: 75%;
    min-width: 100px;
    margin-bottom: 0.3em;
    opacity: 1;
}

.time {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
    margin: 2%;
    opacity: 1;
}

.time-text {
    font-size: 0.8em;
    color: white;
    opacity: 1;
}

.time > img {
    width: 100%;
    min-width: 20px;
    max-width: 50px;
    -webkit-filter: invert(1);
            filter: invert(1);
}

.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    opacity: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.footer-ctr {
    opacity: 1;
    width: 1.5em;
    padding: 0.5em;
    display: block;
    margin: auto;
}

.footer-ctr > img {
    opacity: 1;
    -webkit-filter: invert(1);
            filter: invert(1);
    width: 100%;
}

.camera-sign {
    position: absolute; 
    bottom: 0; 
    padding-bottom: 0.5em;
}


